$couleurPrincipale: #569C87;
$couleurSecondaire: #fdcb46;

@font-face {
    font-family: 'open_sansregular';
    src: url('../fonts/opensans-regular-webfont.woff') format('woff'),
         url('../fonts/opensans-regular-webfont.woff2') format('woff2');
}

body{
    margin:0px;
    font-family: 'open_sansregular';
}

.header{
    width: 100%;
    &__container{
        background-color: $couleurPrincipale;
        display: flex;
        justify-content: center;
        height: 240px;
        margin-bottom: 50px;
        @media (max-width: 768px) {
            height: inherit;
            padding: 7vh;
        }
        &__img{
            width: 40%;
            margin: auto;
        }
    }
}

.main{
    width: 90%;
    margin: auto;
    &__containerImg{
        width: 50px;
    }
}

.footer{
    width: 100%;
    height: 135px;
    background-color: $couleurPrincipale;
    display: flex;
    justify-content: space-between;
    position: fixed;
    bottom: 0;
    @media (max-width: 768px) {
        height: inherit;
        flex-direction: column;
    }
    &__link{
        color: $couleurSecondaire;
        text-decoration: none;
        margin:auto 40px;
        font-size: 30px;
        @media (max-width: 768px) {
            font-size: 15px;
            margin:15px auto;
        }
    }
}